import { Component, Input, Output, EventEmitter  } from '@angular/core'; 


export class ButtonComponent {

     nome: string = 'Ok';
     estilo: string = 'btn-default';
     tipo: string = 'button';
     desabilitado: boolean = false;
     acao = new EventEmitter();
     confirmacao: boolean = false;

    executaAcao() {

        if (this.confirmacao) {
            if(confirm('Confirma?')) {
                this.acao.emit();
            }
            return;
        }
        this.acao.emit();
    }

static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
    selector: 'ksf-button',
    template: `
                <button (click)="executaAcao()" class="btn {{estilo}}" [type]="tipo" [disabled]="desabilitado">{{nome}}</button>
               `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'nome': [{ type: Input },],
'estilo': [{ type: Input },],
'tipo': [{ type: Input },],
'desabilitado': [{ type: Input },],
'acao': [{ type: Output },],
'confirmacao': [{ type: Input },],
};
}
interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
